@import '../basic/layout';


@mixin stile-container {

	.column-2,
	.column-3,
	.column-4 {
		> * {  // One of Columns
			text-align: center;
			> *:not([class]) { text-align: left; }

			> * + * { margin-top: vr(2); }
			> *:first-child.alignleft + *:not([class]) { margin-top: 0; }
			> *:first-child.alignright + *:not([class]) { margin-top: 0; }
		}
	}

	.column-2 {
		@include layout-card(1, vr(2));
		@include for-tablet-up {
			@include layout-card(2, vr(2));
		}
	}

	.column-3 {
		@include layout-card(1, vr(2));
		@include for-tablet-up {
			@include layout-card(3, vr(2));
		}
	}

	.column-4 {
		@include layout-card(1, vr(2));
		@include for-tablet-portrait-up {
			@include layout-card(2, vr(2));
		}
		@include for-tablet-landscape-up {
			@include layout-card(4, vr(2));
		}
	}

}
